@font-face {
  font-family: "ComicFont";
  src: url("./fonts/comic/qdbettercomicsansalternates-z8823-webfont.woff") format("woff");
}

body {
  width: 100%;
  height: 100%;
  color: #002d6a;
  font-family: 'ComicFont', Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

.container {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.interactable-ui {
  pointer-events: auto;
}





@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: #b9b1c3;
  overflow: hidden;
}

.background span {
  width: 16vmin;
  height: 16vmin;
  border-radius: 16vmin;
  backface-visibility: hidden;
  position: absolute;
  animation: move;
  animation-duration: 24;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}


.background span:nth-child(0) {
  color: #9c7ea9;
  top: 88%;
  left: 48%;
  animation-duration: 77s;
  animation-delay: -161s;
  transform-origin: -13vw -13vh;
  box-shadow: -32vmin 0 4.034590845439112vmin currentColor;
}

